@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  position: relative;
}
.offScroll {
  overflow: hidden;
}
* {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
/*HEADER*/
.header {
  display: block;
  position: relative;
  min-height: 56px;
}
@media (min-width: 768px) {
  .header {
    height: min-content;
    padding: 16px 32px;
  }
}
@media (min-width: 1200px) {
  .header {
    padding: 18px 76px;
  }
}
.header__link {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
@media (min-width: 1200px) {
  .header__link {
    background-image: url("data:image/svg+xml,%3Csvg width='187' height='95' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M186.6-2H0l80.5 90.5c7.1 7.9 18.6 7.9 25.7 0L186.6-2z' fill='%2381B3D3'/%3E%3C/svg%3E");
    background-position: top;
    background-repeat: no-repeat;
    width: 186px;
    height: 96px;
    text-align: center;
  }
}
.header__image {
  width: 101px;
  height: 85px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .header__image {
    content: url("../../img/logo-sedona.svg");
  }
}
.navigation {
  position: relative;
  z-index: 1;
}
.navigation--nojs {
  z-index: 0;
}
@media (min-width: 768px) {
  .navigation {
    z-index: 0;
  }
}
/*nav btn*/
.navigation__dropdown-btn {
  position: absolute;
  top: 28px;
  transform: translateY(-50%);
  left: calc(100% - 50px);
  padding: 0;
  margin: 0;
  z-index: 2;
  background-color: transparent;
  border: none;
  width: 23px;
  height: 22px;
  background-image: url("../img/menu-burger.svg");
  background-repeat: no-repeat;
}
.navigation__dropdown-btn--active {
  background-image: url("../img/menu-burger-close.svg");
}
.navigation__dropdown-btn:hover {
  opacity: 0.6;
}
.navigation__dropdown-btn:active {
  opacity: 0.3;
}
.navigation__dropdown-btn--nojs {
  display: none;
}
@media (min-width: 768px) {
  .navigation__dropdown-btn {
    display: none;
  }
}
/*nav block*/
.navigation__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
}
.navigation__list--active {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  background-color: #81B3D2;
  color: #ffffff;
  display: block;
}
.navigation__list--nojs {
  background-color: #81B3D2;
  color: #ffffff;
  display: block;
  padding-top: 70px;
}
@media (min-width: 768px) {
  .navigation__list {
    background-color: transparent;
    display: grid;
    grid-template-columns: 153px 153px 1fr 153px 153px;
  }
  .navigation__list--active {
    background-color: transparent;
    color: #000000;
  }
  .navigation__list::before {
    content: "";
    grid-row: 1 / span 7;
    grid-column-start: 3;
    grid-column-end: 4;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    grid-template-columns: 239px 239px 1fr 239px 239px;
  }
}
/*list elements*/
.navigation__elem {
  height: 55px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.navigation__elem:active {
  background-color: #5496BD;
}
@media (min-width: 768px) {
  .navigation__elem {
    height: auto;
    display: flex;
    justify-content: flex-start;
    box-shadow: none;
    background-color: transparent;
  }
  .navigation__elem:nth-child(4n+3),
  .navigation__elem:nth-child(4n+4) {
    justify-content: flex-end;
  }
  .navigation__elem:active {
    background-color: transparent;
  }
}
.navigation__link {
  text-decoration: none;
  color: #ffffff;
  height: 100%;
  text-align: center;
  display: grid;
  align-content: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 40px;
}
.navigation__link:hover {
  background: #669EC0;
}
.navigation__link:active {
  background: #5496BD;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
  }
  .navigation__link:hover,
  .navigation__link:focus {
    background-color: transparent;
    color: #81B3D2;
  }
  .navigation__link:active {
    background-color: transparent;
    color: #000000;
    opacity: 0.3;
  }
  .navigation__link--current {
    color: #766357;
    pointer-events: none;
  }
}
