.main-logo {
  width: 100%;
  height: 390px;
  background-image: url("../img/main-logo/main-background@1x.jpg");
  background-color: #9fc2dd;
  background-repeat: no-repeat, no-repeat;
  background-position: center, bottom;
  background-size: cover, contain;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .main-logo {
    height: 518px;
  }
}
.main-logo--second-page {
  align-items: center;
  justify-content: center;
  height: 158px;
}
@media (min-width: 768px) {
  .main-logo--second-page {
    height: 290px;
  }
}
@media (min-width: 1200px) {
  .main-logo--second-page {
    height: 302px;
  }
}
.main-logo::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  bottom: 0;
  left: 0;
}
.main-logo::after {
  content: "";
  position: absolute;
  background-image: url("../img/main-logo/main-logo-triangle.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  bottom: 1px;
  left: 0;
  width: 100%;
  height: 57px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-logo {
    background-image: url("../img/main-logo/main-background@2x.jpg");
  }
}
.main-logo__sedona {
  width: 282px;
  height: 214px;
}
@media (min-width: 768px) {
  .main-logo__sedona {
    width: 461px;
    height: 350px;
  }
  .main-logo__sedona--second-page {
    width: 229px;
    height: 39px;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-logo__sedona--second-page {
    width: 456px;
    height: 77px;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .main-logo__sedona--second-page {
    width: 459px;
    height: 80px;
  }
}
