.gallery-info__text {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
.gallery-info__text--br {
  display: none;
}
@media (min-width: 464px) {
  .gallery-info__text--br {
    display: block;
  }
}
.pic-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .pic-list {
    width: 648px;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
@media (min-width: 1200px) {
  .pic-list {
    width: 1200px;
  }
}
.picture-elem {
  border-bottom: 2px solid #E5E5E5;
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  padding: 22px 0 20px 0;
}
@media (min-width: 768px) {
  .picture-elem {
    padding: 0;
    border-bottom: none;
  }
  .picture-elem--main {
    width: 648px;
    min-height: 400px;
  }
  .picture-elem--second {
    width: 324px;
    min-height: 350px;
  }
}
@media (min-width: 1200px) {
  .picture-elem {
    position: relative;
  }
  .picture-elem--main {
    width: 1200px;
  }
  .picture-elem--second {
    width: 400px;
    min-height: 267px;
  }
}
.picture-elem__info {
  padding-top: 10px;
  align-self: stretch;
  display: flex;
  justify-content: space-between;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .picture-elem__info {
    background-color: #f2f2f2;
    align-items: center;
    padding: 20px;
    width: auto;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .picture-elem__info {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    color: #ffffff;
    background: rgba(0, 0, 0, 0.3);
  }
}
.picture-elem__picture {
  display: flex;
  margin: 0 auto;
}
.picture-elem__picture--main {
  background-color: #e09243;
}
.picture-elem__picture--second {
  background-color: #d18d77;
}
.picture-elem__picture--third {
  background-color: #404b37;
}
.picture-elem__picture--fourth {
  background-color: #dfb39a;
}
@media (min-width: 768px) {
  .picture-elem__picture {
    margin: 0;
    width: 100%;
  }
}
/*text style*/
.picture-elem__title {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
}
.picture-elem__author {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
@media (min-width: 1200px) {
  .picture-elem__author {
    color: #ffffff;
  }
}
.picture-elem__like-btn {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
}
@media (max-width: 768px) {
  .picture-elem__like-btn {
    margin-top: 10px;
  }
}
@media (min-width: 1200px) {
  .picture-elem__like-btn {
    color: #ffffff;
  }
}
.picture-elem__like-icon {
  margin-right: 10px;
  margin-bottom: 6px;
}
@media (min-width: 1200px) {
  .picture-elem__like-icon > path {
    opacity: 1;
    fill: #ffffff;
  }
}
.video-player {
  display: grid;
  grid-template-rows: min-content min-content;
  grid-gap: 33px;
  width: 100%;
}
@media (min-width: 768px) {
  .video-player {
    max-width: 648px;
    grid-gap: 62px;
  }
}
@media (min-width: 1200px) {
  .video-player {
    max-width: 800px;
    grid-gap: 66px;
  }
}
.video-player__description {
  padding: 0 26px 0 26px;
}
.video-player__title {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .video-player__title {
    font-size: 30px;
    line-height: 26px;
  }
}
.video-player__subtitle {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
.player {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.player__video {
  width: 100%;
  height: 201px;
}
@media (min-width: 768px) {
  .player__video {
    height: 500px;
  }
}
.player__video[poster] {
  height: 100%;
  width: 100%;
}
.player__controls {
  position: absolute;
  bottom: -66px;
  left: 0;
  right: 0;
  height: 66px;
  width: calc(100% - 40px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  margin: 0 20px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .player__controls {
    margin: 0;
    padding: 0 35px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.6);
    height: 93px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.player__status-wrap {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 23px;
  top: -45px;
  left: 0;
  right: 0;
  height: 45px;
  background: rgba(0, 0, 0, 0.6);
}
@media (min-width: 768px) {
  .player__status-wrap {
    position: static;
    background: transparent;
    height: auto;
  }
}
.player__status-bar {
  width: 100%;
}
@media (min-width: 768px) {
  .player__status-bar {
    width: 389px;
  }
}
@media (min-width: 1200px) {
  .player__status-bar {
    width: 540px;
  }
}
.video-btn {
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .video-btn:hover {
    opacity: 0.6;
  }
  .video-btn:active {
    opacity: 0.3;
  }
}
.player__replay {
  width: 16px;
  height: 20px;
  background-image: url("../../img/video/replay-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
}
@media (min-width: 768px) {
  .player__replay {
    background-image: url("../../img/video/replay-desctop.svg");
  }
}
.player__subtitles {
  width: 32px;
  height: 20px;
  background-image: url("../../img/video/subtitles-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
  margin-right: 31px;
}
@media (max-width: 769px) {
  .player__subtitles:hover {
    background-image: url("../../img/video/subtitles-mobile--hover.svg");
  }
  .player__subtitles:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .player__subtitles {
    background-image: url("../../img/video/subtitles-desctop.svg");
  }
}
.player__full-screen {
  width: 20px;
  height: 20px;
  background-image: url("../../img/video/fullscreen-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
}
@media (max-width: 769px) {
  .player__full-screen:hover {
    background-image: url("../../img/video/fullscreen-mobile--hover.svg");
  }
  .player__full-screen:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .player__full-screen {
    background-image: url("../../img/video/fullscreen-desctop.svg");
  }
}
.custom-video-range {
  -webkit-appearance: none;
  background: transparent;
}
.custom-video-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background: #ffffff;
  cursor: pointer;
  margin-top: -6px;
}
.custom-video-range::-moz-range-thumb {
  border: none;
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background: #ffffff;
  cursor: pointer;
}
.custom-video-range::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.3);
}
.custom-video-range:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
.custom-video-range::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.3);
}
/*FORMS PAGE*/
.form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-gap: 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .form {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content;
    row-gap: 50px;
    width: 648px;
  }
}
@media (min-width: 1200px) {
  .form {
    width: 960px;
  }
}
/*standart style for all fieldset blocks*/
.form__item {
  border: none;
  padding-bottom: 25px;
  margin: 0 auto;
  width: 320px;
  position: relative;
}
.form__item:nth-child(-n+4)::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  margin-left: calc((320px - 100vw) / 2);
  height: 1px;
  background-color: #E5E5E5;
}
@media (min-width: 768px) {
  .form__item {
    width: 324px;
    padding: 10px;
    margin: 0;
  }
  .form__item::before {
    display: none;
  }
}
.form__title {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  position: relative;
}
.form__title--visited {
  margin-bottom: 21px;
}
@media (min-width: 768px) {
  .form__title {
    font-size: 21px;
    line-height: 26px;
  }
  .form__title--visited {
    margin-bottom: 0;
  }
  .form__title--line::before,
  .form__title--line::after {
    content: "";
    position: absolute;
    width: 123px;
    height: 1px;
    background-color: #DBDBDB;
  }
  .form__title--line::before {
    left: 0;
    top: 50%;
  }
  .form__title--line::after {
    right: 0;
    top: 50%;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .form__title--line::before,
  .form__title--line::after {
    width: 280px;
  }
}
/*forms elems*/
.form__named {
  order: 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 20px;
}
@media (min-width: 768px) {
  .form__named {
    order: 2;
    display: flex;
    flex-direction: column;
    padding-top: 54px;
    margin-left: auto;
    margin-right: 0;
  }
}
.form__contact {
  order: 2;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 14px;
}
@media (min-width: 768px) {
  .form__contact {
    order: 3;
    grid-column-start: 1;
    grid-column-end: 3;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
  }
}
@media (min-width: 1200px) {
  .form__contact {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 71px;
    padding-bottom: 51px;
  }
}
.form__impressions {
  order: 3;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}
@media (min-width: 768px) {
  .form__impressions {
    order: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 50px;
    position: relative;
  }
  .form__impressions::after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 1px;
    background-color: #DBDBDB;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .form__impressions::after {
    right: -150px;
  }
}
.form__visited {
  order: 4;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
@media (min-width: 768px) {
  .form__visited {
    grid-column-start: 1;
    grid-column-end: -1;
    width: 100%;
    display: grid;
    grid-row-gap: 30px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-column-gap: 44px;
  }
  .form__visited > .form__title {
    grid-column-start: 1;
    grid-column-end: -1;
  }
}
@media (min-width: 1200px) {
  .form__visited {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 47px;
    padding-bottom: 73px;
  }
}
.form__message {
  order: 5;
  display: grid;
  grid-gap: 15px;
  grid-template-rows: min-content 1fr;
}
@media (min-width: 768px) {
  .form__message {
    grid-column-start: 1;
    grid-column-end: -1;
    display: flex;
    padding-top: 54px;
  }
}
.forms-title {
  padding: 0 10px 16px 10px;
  border-bottom: 2px solid #E5E5E5;
}
.forms-title--wrap {
  margin: 0 auto;
  margin-top: 36px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .forms-title {
    border-bottom: none;
    padding: 0 10px 0 10px;
  }
  .forms-title--wrap {
    width: auto;
    margin-top: 70px;
    margin-bottom: 70px;
  }
}
@media (min-width: 1200px) {
  .forms-title--wrap {
    margin-top: 114px;
    margin-bottom: 60px;
  }
}
.forms-title__text {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .forms-title__text {
    font-size: 30px;
    line-height: 26px;
  }
}
.forms-title__sub-text {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 24px;
  color: #333333;
}
.radio {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
}
.radio:hover .radio__custom-check,
.radio:focus .radio__custom-check {
  background-color: #E5E5E5;
}
.radio__text {
  max-width: 190px;
}
.radio__buton {
  position: absolute;
  appearance: none;
}
.radio__buton:focus ~ .radio__custom-check {
  background-color: #E5E5E5;
}
.radio__buton:disabled ~ .radio__custom-check {
  background-color: #D5D5D5;
}
.radio__buton:checked ~ .radio__custom-check::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #333333;
  opacity: 0.3;
}
.radio__custom-check {
  width: 48px;
  height: 48px;
  margin-right: 23px;
  background-color: #f2f2f2;
  border-radius: 50%;
  position: relative;
}
.standart-input {
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #333333;
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input--name {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.standart-input--long {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-row-gap: 8px;
}
@media (min-width: 1200px) {
  .standart-input--long {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.standart-input__custom-elem {
  height: 58px;
  width: 58px;
  background-color: #E6E6E6;
  grid-row: 2 \ -3;
}
.standart-input__custom-elem--tel {
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.6 7.8c1.4 2.8 3.8 5.1 6.6 6.6l2.2-2.2c.3-.3.7-.4 1-.2 1.1.4 2.3.6 3.6.6.6 0 1 .4 1 1V17c0 .6-.4 1-1 1C7.6 18 0 10.4 0 1c0-.6.4-1 1-1h3.5c.6 0 1 .4 1 1 0 1.2.2 2.4.6 3.6.1.3 0 .7-.2 1L3.6 7.8z' fill='%23010101'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}
.standart-input__custom-elem--email {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 8c-.3 0-.5-.1-1-.3L0 4v7c0 .5.5 1 1 1h14c.5 0 1-.5 1-1V4L9 7.7c-.5.2-.7.3-1 .3zm7-8H1C.5 0 0 .5 0 1v.8L8 6l8-4.2V1c0-.5-.5-1-1-1z' fill='%23000'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}
.standart-input__input {
  padding: 14px 22px 10px 18px;
  background-color: #f2f2f2;
  border: 4px solid transparent;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input--name {
  flex-grow: 1;
  margin-left: 10px;
  width: 100%;
}
@media (min-width: 1200px) {
  .standart-input__input--name {
    width: 350px;
  }
}
.standart-input__input--long {
  width: 100%;
  grid-row: 2 \ -3;
}
@media (min-width: 768px) {
  .standart-input__input--long {
    height: 58px;
    width: auto;
  }
}
@media (min-width: 1200px) {
  .standart-input__input--long {
    margin-left: 16px;
    width: 257px;
  }
}
.standart-input__input::placeholder {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  opacity: 1;
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input::-webkit-input-placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input::-moz-placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input:-ms-input-placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input:-moz-placeholder {
  font-family: "PT Sans", "Arial", sans-serif;
}
.standart-input__input:hover {
  background-color: #E5E5E5;
}
.standart-input__input:focus {
  background-color: transparent;
  border: 4px solid #E5E5E5;
}
.standart-input__input:disabled,
.standart-input__input:disabled ~ .standart-input__custom-elem {
  background-color: #D5D5D5;
}
@media (min-width: 1200px) {
  .standart-input__hidden {
    display: none;
  }
}
.radio-square {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  color: #000000;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-end;
}
.radio-square:hover .radio-square__custom-check,
.radio-square:focus .radio-square__custom-check {
  background-color: #E5E5E5;
}
.radio-square:not(:last-child) {
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .radio-square {
    margin-bottom: 0;
  }
  .radio-square:nth-child(even) {
    justify-content: flex-start;
  }
}
@media (min-width: 1200px) {
  .radio-square {
    justify-content: center;
    flex-grow: 1;
    width: max-content;
  }
}
.radio-square__text {
  max-width: 160px;
}
.radio-square__input {
  position: absolute;
  appearance: none;
}
.radio-square__input:focus ~ .radio-square__custom-check {
  background-color: #E5E5E5;
}
.radio-square__input:disabled ~ .radio-square__custom-check {
  background-color: #D5D5D5;
}
.radio-square__input:checked ~ .radio-square__custom-check {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.925 1.325l-1.1-1.1c-.3-.3-.9-.3-1.2 0l-11.9 11.9-6.2-6.2c-.3-.3-.9-.3-1.2 0l-1.1 1.1c-.3.3-.3.9 0 1.2l7.8 7.8c.2.2.4.2.6.2.2 0 .4-.1.6-.2l13.5-13.5c.5-.3.5-.8.2-1.2z' fill='%23000'/%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
}
.radio-square__custom-check {
  width: 51px;
  height: 51px;
  border-radius: 5px;
  margin-right: 30px;
  background-color: #f2f2f2;
}
.message-input {
  box-sizing: border-box;
  display: block;
  background-color: #f2f2f2;
  padding: 14px 22px 10px 22px;
  border: 4px solid transparent;
  width: 100%;
  height: 160px;
  resize: none;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
