    width: 960px;
  }
}
.message-input::placeholder {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  opacity: 1;
}
.message-input:hover {
  background-color: #E5E5E5;
}
.message-input:focus {
  background-color: transparent;
  border: 4px solid #E5E5E5;
}
.message-input:disabled {
