.form-modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.form-modal--error {
  display: none;
}
.form-modal--success {
  display: none;
}
.modal-open {
  display: block;
}
.form-modal__wrap {
  width: 100vw;
  height: 100vh;
  background-color: rgba(222, 222, 222, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.form-modal__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.form-modal__content {
  z-index: 1;
  max-width: 320px;
  height: min-content;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
}
.form-modal__content--error {
  padding: 40px 41px 31px 41px;
}
@media (min-width: 768px) {
  .form-modal__content--error {
    max-width: 400px;
    padding: 35px 47px 49px 47px;
  }
}
.form-modal__content--success {
  padding: 38px 0 0 0;
}
@media (min-width: 768px) {
  .form-modal__content--success {
    max-width: 640px;
  }
}
.form-modal__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  padding: 0;
  color: #000000;
  margin: 0 0 22px 0;
}
.form-modal__title--success {
  margin: 0 38px 22px 38px;
}
@media (min-width: 768px) {
  .form-modal__title--success {
    margin: 0 98px 31px 98px;
  }
}
@media (min-width: 768px) {
  .form-modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}
.form-modal__text {
  font-size: 14px;
  line-height: 24px;
  padding: 0;
  color: #333333;
  margin: 0 0 22px 0;
}
.form-modal__text--success {
  margin: 0 38px 22px 38px;
}
@media (min-width: 768px) {
  .form-modal__text--success {
    margin: 0 98px 31px 98px;
  }
}
.form-modal__btn-wrap {
  padding: 20px;
