  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .form-modal__btn-wrap {
    padding: 44px 91px;
  }
}
.form-modal__btn {
  background-color: #81B3D2;
  margin: 0;
  border: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
