.footer {
  height: min-content;
  padding: 28px 0 26px 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 40px;
  align-self: center;
}
.footer--container {
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .footer {
    width: auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    padding: 36px 60px 36px 45px;
  }
}
@media (min-width: 1200px) {
  .footer {
    padding: 36px 120px 36px 109px;
  }
}
.footer__hash {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .footer__hash {
    justify-content: flex-start;
  }
}
.footer__hash-link {
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .footer__hash-link {
    text-align: left;
  }
}
.footer__link {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .footer__link {
    justify-content: flex-end;
  }
}
.footer__link-elem {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  line-height: 26px;
