.player {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.player__video {
  width: 100%;
  height: 201px;

  @media (min-width: @tablet-width) {
    height: 500px;
  }

  &[poster] {
    height: 100%;
    width: 100%;
  }
}

//controls block
.player__controls {
  position: absolute;
  bottom: -66px;
  left: 0;
  right: 0;
  height: 66px;
  width: calc(100% - 40px);

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  margin: 0 20px;
  background-color: @input-bg;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding: 0 35px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(000, 000, 000, 0.6);
    height: 93px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

//input type range, status bar
.player__status-wrap {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 23px;
  top: -45px;
  left: 0;
  right: 0;
  height: 45px;
  background: rgba(000, 000, 000, 0.6);

  @media (min-width: @tablet-width) {
    position: static;
    background: transparent;
    height: auto;
  }
}

.player__status-bar {
  width: 100%;

  @media (min-width: @tablet-width) {
    width: 389px;
  }

  @media (min-width: @desctop-width) {
    width: 540px;
  }
}

//controls buttons
.video-btn { //standart style for controls buttons
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.player__replay {
  width: 16px;
  height: 20px;
  background-image: url("../../img/video/replay-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;

  @media (min-width: @tablet-width) {
    background-image: url("../../img/video/replay-desctop.svg");
  }
}

.player__subtitles {
  width: 32px;
  height: 20px;
  background-image: url("../../img/video/subtitles-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
  margin-right: 31px;

  @media (max-width: @tablet-width + 1) {
    &:hover {
      background-image: url("../../img/video/subtitles-mobile--hover.svg");
    }

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: @tablet-width) {
    background-image: url("../../img/video/subtitles-desctop.svg");
  }
}

.player__full-screen {
  width: 20px;
  height: 20px;
  background-image: url("../../img/video/fullscreen-mobile.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;

  @media (max-width: @tablet-width + 1) {
    &:hover {
      background-image: url("../../img/video/fullscreen-mobile--hover.svg");
    }

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: @tablet-width) {
    background-image: url("../../img/video/fullscreen-desctop.svg");
  }
}
